import { IViewModel } from "../../domain/viewModel/IViewModel";
import { IPresentation } from "../../domain/presentation/IPresentation";
import { container, inject, injectable } from "../../domain/core/diContainer";
import { ICreateModule, ICreateModuleParam } from "../../domain/useCase/ICreateModule";
import { IUseCaseExecutor } from "../../domain/useCase/IUseCaseExecutor";
import { ICreateModel } from "../../domain/useCase/ICreateModel";
import { IViewModelProxifier } from "../../domain/presentation/IViewModelProxifier";

@injectable()
class HtmlProxifier implements IViewModelProxifier {
    markRaw<T extends object>(obj: T | undefined): T | undefined {
        return obj;
    }
    public isProxified() { return true; }
    public proxify<T>(obj: T) { return obj; }
}

container.register<IViewModelProxifier>(IViewModelProxifier, HtmlProxifier);

@injectable()
export class HtmlPresentation implements IPresentation {
    constructor(
        @inject(IViewModel) private readonly viewModel: IViewModel,
        @inject(IUseCaseExecutor) private readonly executor: IUseCaseExecutor,
    ) { }

    public start(): void {
        const btnDiv = document.createElement("div");;
        const btnModule = document.createElement("button");
        let createdParentObjectId: string | undefined;
        btnModule.innerText = "create module";
        btnModule.onclick = () => {
            this.executor.execute(async () => {
                const module: ICreateModuleParam = { name: "alper3" };
                const createdModule = await this.executor.executeUseCase<ICreateModule>(ICreateModule, module);
                createdParentObjectId = createdModule.ID;
            }, { loading: true });
        }

        btnDiv.appendChild(btnModule);

        const btnModel = document.createElement("button");
        btnModel.innerText = "create model";
        btnModel.onclick = () => {
            this.executor.executeUseCase<ICreateModel>(ICreateModel, { name: "model1", parentObjectId: createdParentObjectId!, modelType: "qjson", modelAdditionals: { qjsonType: "qjson" } });
        }
        btnDiv.appendChild(btnModel);

        document.body.appendChild(btnDiv)
    }
}